import React, { Component } from "react";
import Slider from "react-slick";

import Layout from "../components/layout"
import SEO from "../components/seo"
import bg from "../images/bg.jpg"
import bgLogo from "../images/wallpaper-logo.png"
import thumbnail from "../images/exampleVid.png"

const settings = {
  dots: false,
  infinite: false,
  speed: 500,
  slidesToShow: 3,
  slidesToScroll: 3,
  nextArrow: <NextArrow />,
  prevArrow: <PrevArrow />
};

function NextArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      style={{ ...style, display: "block", background: "red", color: "black"}}
      onClick={onClick}
    />
  );
}

function PrevArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      style={{ ...style, display: "block", background: "green" }}
      onClick={onClick}
    />
  );
}


const IndexPage = () => (
  <Layout>
    <SEO title="Home" />

    <div class="main">
      <img href="/" src={bg} alt="Background Wallpaper" id="bg-img"></img>
      <img src={bgLogo} alt="TEDxJHU Logo" id="bg-logo"></img>
      <a href="/2020-event">
        <button class="enter-button">View 2020 Event</button>
      </a>
    </div>

    <div class="recent-talks">
        <p class="recent-talks-header">Recent Talks</p>
        <Slider {...settings}>
          <div class="recent-talks-videos">
            <img href="/" src={thumbnail} alt="Background Wallpaper" class="thumbnail"></img>
          </div>  
          <div class="recent-talks-videos">
            <img href="/" src={thumbnail} alt="Background Wallpaper" class="thumbnail"></img>
          </div>  
          <div class="recent-talks-videos">
            <img href="/" src={thumbnail} alt="Background Wallpaper" class="thumbnail"></img>
          </div>  
          <div class="recent-talks-videos">
            <img href="/" src={thumbnail} alt="Background Wallpaper" class="thumbnail"></img>
          </div>  
        </Slider>
    </div>
  </Layout>
)

export default IndexPage
